body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;

}

#yellowBackground {
  position: absolute;
  background: #FFCC01;
  width: 100%;
  height: 30%;
  overflow: hidden;
}

#roundBackground {
  position: absolute;
  top: 15%;
  left: -85%;
  width: 270%;
  height: 100%;
  background: #FFFFFF;
  border-radius: 100% 100% 0px 0px;
  /* border: black 1px solid; */
  background: white;
  overflow: hidden;
}

#yellowBackground p {
  text-align: center;
}

#selectStation {
  position: absolute;
  width: 40%;
  height: 6%;
  top: 20%;
  left:50%;
  transform: translateX(-50%);
  background: #FFFFFF;
  border: 1px solid #DDDDDD;
  box-sizing: border-box;
  border-radius: 10px;
  text-align-last: center;
  /* padding: 0.1% 0; */
}

#inLine {
  position: absolute;
  width: 50%;
  height: 23%;
  top: 30%; 
  left: 50%;
  transform: translateX(-50%);
  background: #FFFFFF;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  text-align: center;
}
#outLine {
  position: absolute;
  width: 50%;
  height: 23%;
  top: 58%; 
  left: 50%;
  transform: translateX(-50%);
  background: #FFFFFF;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  text-align: center;
}

.subway {
  position: relative;
  width: 60%;
  margin: 0;
  padding: 0;
}
.subway:before {
  content: '';
  position: absolute;
  width: 100%;
  top: 50%;
  border-top: 2px solid #ccc;
}
.subway img {
  position: relative;
  width: 20%;
  left: 80%;
}

#leftCount {
  position: absolute;
  width: 40%;
  height: 5%;
  left: 60%;
  top: 90%;
}

